.App {
	text-align: center;
}
/* Blue: #20948B, Orange: #DE7A22, Sand: #F4CC70, Green: #6AB187 */
.App-logo {
	height: 50px;
	-webkit-filter: invert(100%);
	        filter: invert(100%);
}

.App-header {
	background-color: #20948b;
	height: 50px;
	padding: 20px;
	color: white;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: start;
	    justify-content: flex-start;
}

.App-title {
	font-size: 2em;
	margin: 0 0 0 10px;
}

.App-intro {
	font-size: large;
}

.App-body {
	display: -ms-flexbox;
	display: flex;
	padding: 10px;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
	-ms-flex-pack: center;
	    justify-content: center;
}

.box {
	width: 80%;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-pack: center;
	    justify-content: center;
	min-width: 300px;
	max-width: 600px;
	margin: 10px;
	padding: 10px;
}

.func-input {
	display: inline;
}

.axis-input {
	width: 4rem;
	margin: 10px;
}

.subtext {
	font-size: 0.8rem;
	margin: -7px;
	color: grey;
}
h4 {
	margin: 10px;
}
.hidden-submit {
}
.card {
	-webkit-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
	        box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}



